<template>
<div id="container">
  
  <HelloWorld msg="Super Converter"/>
  <div><input type="text" v-model="fromValue" placeholder="enter amount"></div>
  <div><input type="text" v-model="fromUnit" placeholder="enter current unit"></div>
  <div>to</div>
  <div><input type="text" v-model="toUnit" placeholder="enter wanted unit"></div>

  <div><p id="result">{{fromValue}} {{fromUnit}} equals  {{result}} {{toUnit}} </p></div>
</div>
</template>
<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  methods:{
        toBanana: function(value,unit){
            switch(unit.toLowerCase()){
              case("banana"):
                  return value;
              case("potato"):
                  return value*7;
              case("tomato"):
                   return value*3;
              default:
                   console.log("Convervsion failed")
                   return value;
            }
        }
  },

props: {
    physicalQuantity: Object, // props is basically just data passed from a parent to a child component. App.vue is our parent here and it passes an Object to this variable
  },
  data: function () {
    return {
      fromValue: "",
      fromUnit: "",
      toUnit: "",
    };
  },




  computed: {
      result: function(){
            let value = parseFloat(this.fromValue);  // whatever the user types into the <input> Element, it turns out to be a string. We have to convert it to a number(float)   <div><p id="result">{{fromValue}} {{fromUnit}} equals {{result}} {{toUnit}} </p></div>
            if(isFinite(value)){
                        let ValueInBanana = this.toBanana(value,this.fromUnit);  // we use "minutes" as an intermediary unit
                        switch(this.toUnit.toLowerCase()){
                        case("banana"):
                            return parseFloat((ValueInBanana).toFixed(5));   // limit to 5 decimals and parseFloat() it to remove redundant 0s
                        case("potato"):
                            return parseFloat((ValueInBanana/7).toFixed(5));
                        case("tomato"):
                            return parseFloat((ValueInBanana/3).toFixed(5));
                        default:
                            console.log("Error while converting time - resultUnit not detected!");
                            return "value";
                         }
              
            }      
            return (this.fromValue);  // if the user didn't type in a number
      }
  }

  
}
</script>



































